import logo from "../asset/logo.webp";
import { links } from "../constant/constant";

export const Footer = () => {
  return (
    <footer className="pt-20 px-6 container">
      <div className="grid grid-cols-1 md:grid-cols-7 gap-y-4 md:gap-y-0 py-4">
        <div className="col-span-2 ">
          <a href="/">
            <img
              src={logo}
              alt="logo"
              width="240"
              height="160"
              className="w-56 md:w-auto"
            />
          </a>
        </div>
        <ul className="border-r border-gray-400  text-lg lg:text-lg col-span-2">
          {links.map((arg) => (
            <li key={arg[0]} className="py-2">
              <a
                href={arg[0]}
                className={`
                   md:px-4 rounded-md hover:text-gray-500 `}
              >
                <strong>{arg[1]}</strong>
              </a>
            </li>
          ))}
        </ul>
        <div className="sm:pl-2 md:pl-4 space-y-2 col-span-3">
          <h2 className="flex flex-col font-bold">
            SIGNUP UP FOR UPDATES <span> STRAIGHT TO YOUR INBOX</span>
          </h2>
          <div className="grid grid-cols-1 space-y-2 gap-4 pt-4 sm:grid-cols-3 sm:space-x-1">
            <input
              type="email"
              placeholder="your email*"
              className="rounded-xl px-4 py-3 sm:col-span-2 bg-input border-2 "
            />
            <div>
              <button className="px-4 py-3 rounded-md border border-white">
                Subscribe
              </button>
            </div>
          </div>
        </div>
      </div>
      <p className=" text-center py-10 text-lg">
        COPYRIGHT 2021 &copy; SINGULARITY EDUCTION HOLDINGs
      </p>
    </footer>
  );
};
