import { FC, Fragment, useEffect } from "react";
import { useState } from "react";
import { comment } from "../constant/constant";
import profile from "../asset/profile.jpg";
import quotes from "../asset/quotes.webp";

export const Comment = () => {
  const [active, setActive] = useState(0);

  useEffect(() => {
    setTimeout(() => {
      setActive((prev) => (prev === comment.length - 1 ? 0 : ++prev));
    }, 5000);
  }, [active]);

  return (
    <div className="pt-36" id="comment">
      <h2 className="text-center text-2xl px-8 sm:text-3xl font-bold ">
        Praise for A360 From Our Members
      </h2>

      {/* small screen */}
      <div className="space-x-4 px-8 py-14 md:hidden" id="comment">
        {comment.map((arg, index) => {
          return (
            <Fragment key={arg.name}>
              {active === index && <Post con={arg} />}
            </Fragment>
          );
        })}

        {/* carasoual button */}
        <div className="py-16 flex justify-center space-x-2">
          {comment.map((arg, index) => (
            <button
              key={arg.name}
              onClick={() => setActive(index)}
              className={`w-2 h-2 rounded-md bg-overley ${
                active === index ? "opacity-100" : " opacity-10"
              }`}
            ></button>
          ))}
        </div>
      </div>

      {/* large screen */}
      <div className="py-16 hidden md:block">
        {comment.map((arg, index) => {
          return (
            active === index && (
              <div key={arg.name} className="grid grid-cols-2 gap-6">
                <Post con={arg} />{" "}
                {index === comment.length - 1 ? (
                  ""
                ) : (
                  <Post con={comment[index + 1]} />
                )}
              </div>
            )
          );
        })}

        {/* carasoual button */}
        <div className="py-16 flex justify-center space-x-2">
          {comment.map((arg, index) => (
            <button
              key={arg.name}
              onClick={() => setActive(index)}
              className={`w-3 h-3 rounded-md bg-overley ${
                active === index ? "opacity-100" : "opacity-10"
              }`}
            ></button>
          ))}
        </div>
      </div>
    </div>
  );
};

const Post: FC<{ con: { name: string; job: string; comment: string } }> = ({
  con,
}) => {
  return (
    <>
      <div className="border rounded-xl transition duration-300 py-20 lg:py-28 relative">
        <div className="rounded-full flex items-center justify-center border-2 w-16 h-16 text-center absolute left-10 -top-8 bg-background">
          <img src={quotes} width="24" alt="quotes" />
        </div>

        <blockquote className="px-8">
          <strong className="font-semibold">"{con.comment}"</strong>
        </blockquote>
        <div className="absolute z-10 flex justify-end items-end top-0 left-0 right-10 -bottom-10">
          <Card name={con.name} job={con.job} />
        </div>
      </div>
    </>
  );
};

const Card: FC<{ name: string; job: string }> = ({ name, job }) => {
  return (
    <div className=" w-60 grid grid-cols-4 space-x-2 py-2 border px-4 rounded-lg bg-white">
      <div className="h-12 w-18 rounded-full overflow-hidden border">
        <img
          src={profile}
          alt="profile"
          width="18"
          height="12"
          className="w-full h-full"
        />
      </div>
      <div className="self-center col-span-3">
        <h2 className="text-sm md:text-lg text-black">
          <strong>{name}</strong>
        </h2>
        <p className="text-xs md:text-base text-white-lead">{job}</p>
      </div>
    </div>
  );
};
