import programImage from "../asset/program.jpg";

export const Program = () => {
  return (
    <div>
      <h1 className="text-xl md:text-2xl lg:text-3xl font-bold text-center py-10">
        The <span className="underline">3 Key Parts</span> of the A360 Coaching
        Program
      </h1>

      <Panel />
      <Panel overlay="left" />
      <Panel />

      <div className="text-center">
        <button className="border-2 rounded-2xl px-6 py-4 hover:bg-overley hover:border-transparent text-base md:text-lg">
          <strong>Learn More About A360</strong>
        </button>
      </div>
    </div>
  );
};

const Panel = ({ overlay = "right" }) => {
  const i = (
    <img src={programImage} className="w-full hidden md:block" alt="" />
  );
  return (
    <div className="grid md:grid-cols-2 grid-cols-1  place-items-center container py-10 space-x-4">
      {overlay === "left" && i}
      <img src={programImage} className="w-full md:hidden" alt="" />
      <div className="grid place-content-center py-6 md:py-0">
        <h3 className="text-lg font-semibold text-overley">A360 Part</h3>
        <h2 className="text-xl lg:text-2xl font-bold">
          3-Day Summit (February 2-4, 2022)
        </h2>
        <p className="text-base my-4">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Repellat
          laudantium quae optio, animi autem sunt, voluptate eius ipsum omnis
          incidunt sequi fugas.
        </p>
      </div>
      {overlay === "right" && i}
    </div>
  );
};
