import men from "../asset/Joe.webp";

export const PastSpeakers = () => {
  const content = [
    { name: "Joe Gebbia", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
    { name: "Jhon doe", pos: "Co-Founder", img: men },
  ];
  return (
    <div className="py-10 ">
      <div className="grid sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
        {content.map((arg, index) => (
          <div className="relative" key={index}>
            <div className="absolute inset-0 bg-gradient-to-b opacity-70 from-transparent via-transparent to-black"></div>
            <img
              src={arg.img}
              alt="peter"
              className="w-full"
              width="300"
              height="360"
            />
            <div className="absolute inset-0 flex items-end justify-center z-10">
              <div className="text-center space-y-2 pb-4">
                <h3 className="text-xl">
                  <strong>{arg.name}</strong>{" "}
                </h3>
                <p className="text-lg">{arg.pos}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};
