import PlusIcon from "@heroicons/react/solid/PlusIcon";
import MinusIcon from "@heroicons/react/solid/MinusIcon";

import { useState } from "react";
import { FC } from "react";

export const Faq = () => {
  const contents = [
    {
      q: "WHO is the A360 Summit for?",
      a: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium  ratione commodi eum id odio hic, dolorem illum perferendis. Quam, ducimus? ",
    },
    {
      q: "HOW do i APPLY to ATTEND te SUMMIT?",
      a: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium  ratione commodi eum id odio hic, dolorem illum perferendis. Quam, ducimus? ",
    },
    {
      q: "WHO is the A360 Summit for kk?",
      a: " Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium  ratione commodi eum id odio hic, dolorem illum perferendis. Quam, ducimus? ",
    },
  ];
  return (
    <>
      <div className="py-20">
        <h1 className="text-center my-10 text-2xl lg:text-3xl">
          <strong> ABUNDANCE360 SUMMIT FAQ</strong>
        </h1>
        <div className="space-y-3 md:px-10 lg:px-20 flex justify-center flex-col">
          {contents.map((arg) => {
            return <Tab key={arg.q} arg={arg} />;
          })}
        </div>
      </div>
    </>
  );
};

const Tab: FC<{
  arg: {
    a: string;
    q: string;
  };
}> = ({ arg }) => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <>
      <div className="bg-blue-600 px-2 md:px-4 py-5 rounded">
        <button
          className="flex justify-between w-full items-center text-lg"
          onClick={() => setIsOpen((prev) => !prev)}
        >
          {arg.q}
          {isOpen ? (
            <MinusIcon className="h-3 w-3" />
          ) : (
            <PlusIcon className="h-3 w-3" />
          )}
        </button>
        {isOpen && <p className=" mt-2">{arg.a}</p>}
      </div>
    </>
  );
};
