import { Menu } from "@headlessui/react";
import MenuIcon from "@heroicons/react/solid/MenuIcon";
import XIcon from "@heroicons/react/solid/XIcon";
import { links } from "../constant/constant";
import logo from "../asset/logo.webp";
import { Dispatch, SetStateAction, useEffect, useRef, useState } from "react";
import { FC } from "react";

export const Navbar = () => {
  const [isOpen, setOpen] = useState(false);

  const navRef = useRef<null | HTMLElement>(null);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (!navRef.current) return;

      if (window.scrollY > 0) {
        navRef.current.classList.add("bg-background", "shadow-xl", "py-4");
        navRef.current.classList.remove("bg-transparent", "py-8");
      } else {
        navRef.current.classList.add("bg-transparent", "py-8");
        navRef.current.classList.remove("bg-background", "shadow-xl", "py-4");
      }
    });
  }, []);

  return (
    <nav
      ref={navRef}
      className="bg-transparent transition sticky top-0 z-50 py-8 inset-x-0 flex px-6 xl:px-24 justify-between items-center"
    >
      <a href="/">
        <img
          src={logo}
          alt="logo"
          width="200"
          height="50"
          className="w-40 sm:w-48"
        />
      </a>
      <ul className="hidden lg:flex text-xs md:text-base lg:text-lg space-x-2">
        {links.length > 0 &&
          links.map((arg, index) => (
            <li key={index}>
              <a
                className={
                  "px-3 py-2 block rounded-md  hover:text-gray-400 transition-colors"
                }
                href={arg[0]}
                key={arg[0]}
              >
                {arg[1]}
              </a>
            </li>
          ))}
      </ul>

      {/* mobile menu dropdown */}
      <div className="lg:hidden">
        <Menu>
          <Menu.Button>
            <span className="sr-only">menu</span>
            {isOpen ? (
              <XIcon className="h-8 w-10 text-gray-500" />
            ) : (
              <MenuIcon className="h-6 w-6" />
            )}
          </Menu.Button>
          <Menu.Items
            className="absolute h-96 inset-x-0 my-6 focus:outline-none bg-dropdown"
            as="ul"
          >
            <ListMap setOpen={setOpen} />
          </Menu.Items>
        </Menu>
      </div>
    </nav>
  );
};

const ListMap: FC<{
  setOpen: Dispatch<SetStateAction<boolean>>;
}> = ({ setOpen }) => {
  useEffect(() => {
    setOpen(true);
    return () => setOpen(false);
  });
  return (
    <>
      {links.map((arg) => (
        <Menu.Item as="li" key={arg[0]} className="border-t-2">
          {({ active }) => (
            <a
              href={arg[0]}
              className={`${
                active && "text-gray-400"
              } px-7 py-3 block rounded-md hover:text-gray-400 text-lg`}
            >
              <strong>{arg[1]}</strong>
            </a>
          )}
        </Menu.Item>
      ))}
    </>
  );
};
